import React, { Component } from "react";
import { NavItem, Nav, NavDropdown, MenuItem, Form, DropdownButton } from "react-bootstrap";
import { Redirect } from 'react-router-dom'
import {SERVER_URL} from "constants/nodeserver";

class HeaderLinks extends Component {
  constructor(props){
    super(props);
    this.state = {
      notifications:[],
      toCommunity:false,
    }
    this.fetchNotifications(); //to call this function when this component gets initiated
  }

 // //To redirect the user to login page if no valid user is signed in
 //  redirectToCommunity = () => {
 //    console.log('redirectToCommunity called in HeaderLinks')
 //    if(this.state.toCommunity){
 //      return <Redirect to='/community' />
 //    }else{
 //      return null;
 //    }
 //  }

  componentDidUpdate(prevProps) {
  //Typical usage (don't forget to compare props):
  if (this.props.currentUser !== prevProps.currentUser) {
    console.log("current", 'fetchNotifications ran in headerlinks')
    this.fetchNotifications();
  }
  }

  fetchNotifications = () => {
    const currentUser = this.props.currentUser;
    if(currentUser!== undefined && currentUser.id !== null && currentUser.id.toString().length > 0){
     fetch(''+SERVER_URL+ 'getNotifications', {
          method: 'post',
          headers: {'Content-Type': 'application/json',
            },
          body: JSON.stringify({
              userid: this.props.currentUser.id
          })
        }).then(response => response.json())
          .then(ns => {
            console.log('notifications retreived are', ns);
            this.setState({notifications: ns})
          })
          .catch(err => console.log('ERROR in retriving notifications',err))
    }else{
      console.log('Invalid currentUser for fetchNotifications');
    }
  }


  // handleSearch = (e) => {

  //   const searchKeyword = document.getElementById('searchField1').value;
  //     console.log('searchButton clicked', searchKeyword );
  //     this.props.changeSearchUserKeyword(searchKeyword);
  //     this.setState({toCommunity:true});
  // }

  processDate = (postCreated) => {
      //console.log('processDate', postCreated)
      const d = new Date(postCreated);
      const strMonths = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      const month = strMonths[d.getMonth()];
      const hour = d.getHours();
      const minutes = d.getMinutes();
      const day = d.getDate();
      return day + " " + month + " at " + hour + ":" + minutes;
    }


  updateNotificationReadStatus = () => {
    const currentUser = this.props.currentUser;
    if(currentUser!== undefined && currentUser.id !== null && currentUser.id.toString().length > 0){
      var unreadCounter = 0
      this.state.notifications.forEach((n, i) => {
       if(n.is_read === 0)
            unreadCounter++
      })
      if(unreadCounter > 0){
       fetch(''+SERVER_URL+'updateNotificationReadStatus', {
            method: 'post',
            headers: {'Content-Type': 'application/json',
              },
            body: JSON.stringify({
                userid: this.props.currentUser.id
            })
          }).then(response => response.json())
            .then(result => {
              console.log('updateNotificationReadStatus result is', result);
              //making the local notifications read in the state as the database must've been updated
              this.state.notifications.forEach(n => n.is_read = 1);
            })
            .catch(err => console.log('ERROR in updateNotificationReadStatus',err))
      }
  }else{
      console.log('Invalid currentUser for updateNotificationReadStatus');
    }
  }



  render() {
    const ns = this.state.notifications;
    let displayNotifications, unreadCounter = 0;
    if(ns.length === 0){
      displayNotifications = <MenuItem>No items to display</MenuItem>
    }else{
      ns.forEach((n, i) => {
       if(n.is_read === 0)
            unreadCounter++
      })
       displayNotifications = ns.map((n,i) => {
        return <MenuItem className='notificationMenuItem' key={i}>
                    <div className='notificationMenuMsg'>{n.message_body}</div>
                    <div className='notificationMenuTime'>{this.processDate(n.create_time)}</div>
                    <hr style={{margin:'5px 0px 0px 0px',borderBottom: '2px solid #eee'}}/>
               </MenuItem>
      })
    }
    console.log('The HeaderLinks rendered, displayNotifications are:', displayNotifications);
    const notification = (
      <div>
        <i className="fa fa-bell" />
        <b className="caret" />
        {(unreadCounter > 0)?(<span className="notification">{unreadCounter}</span>):null}
        <p className="hidden-lg hidden-md">Notification</p>
      </div>
    );



    return (
      <div>
      {
      //  this.redirectToCommunity()
      }
        <Nav pullRight>
           {/*<NavItem eventKey={3}>
             <Form inline style={{margin:'auto 0px'}}>
             <input type='search' id='searchField1' name='searchKeyword' className='searchTextField' placeholder='Search' size='15'/>
               <button className='searchButton' onClick={this.handleSearch} type='submit'>
                  <i className="fa fa-search" />
               </button>
             </Form>
           </NavItem>
           */}
           <NavDropdown
            eventKey={2}
            title={notification}
            noCaret
            id="basic-nav-dropdown"
            onFocus={()=> this.fetchNotifications()}
            onMouseDown={()=> this.updateNotificationReadStatus()}
          >
            {displayNotifications}
          </NavDropdown>
          <NavDropdown
            eventKey={2}
            noCaret
            title= { <div>
                       {//<i className="fa fa-user"/>
                       }
                       <img alt='user profile' style={{marginRight:'12px', marginLeft:'10px', width:'40px',verticalAlign:'top'}} src={'https://res.cloudinary.com/awesomedeveloper/image/upload/w_60,c_fill,ar_1:1,g_auto,r_max,bo_2px_solid_gray/' + this.props.currentUser.profileimageid} />
                       <p className="hidden-lg hidden-md" style={{display:'inline-block'}}>Account</p>
                     </div>
                   }
            id="basic-nav-dropdown-right"
          >
            <MenuItem eventKey={2.1}>Account</MenuItem>
            <MenuItem eventKey={2.2}>Profile</MenuItem>
            <MenuItem eventKey={2.3}>Settings</MenuItem>
            <MenuItem divider />
            <MenuItem eventKey={2.5} onClick={() => {this.props.logoutUser();}}>Logout</MenuItem>
          </NavDropdown>
        </Nav>
      </div>
    );
  }
}

export default HeaderLinks;
