import React, {Component} from 'react';
import {SERVER_URL} from "../../constants/nodeserver";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee, faThumbsUp } from '@fortawesome/free-solid-svg-icons'
 
const element = <FontAwesomeIcon icon={faCoffee} />

class Post extends Component{
	constructor(props){
	    super(props);
	    this.state = {
	      likes:0,
	      isLikedByCurrentUser: (this.props.post.likeid === null)?false:true,
	    }
  	}

  	toggleIsLikedByCurrentUser = () => {
  		if(this.state.isLikedByCurrentUser === true)
  			this.setState({isLikedByCurrentUser: false})
  		else
  			this.setState({isLikedByCurrentUser: true})
  	}

  	processDate = (postCreated) => {
  		//console.log('processDate', postCreated)
  		const d = new Date(postCreated);
  		const strMonths = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
  		const month = strMonths[d.getMonth()];
  		const hour = d.getHours();
  		const minutes = d.getMinutes();
  		const day = d.getDate();
  		return day + " " + month + " at " + hour + ":" + minutes;
  	}

  	componentDidMount(){
  		this.retrieveLikes();
  	}

  	retrieveLikes = () => {
  		const postid = this.props.post.postid;
  		fetch(''+SERVER_URL+'getLikeCount', {
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            postid
        })
      }).then(response => response.json())
        .then(count => this.setState({likes:Number(count)}))
        .catch(err => console.log('ERROR in retriving likes',err))
  	}


  	// submitLike = () => {
  	// 	const userid = this.props.currentUser.id;
  	// 	const postid = this.props.post.postid;
  	// 	console.log('userid in post', userid, 'postid is ', postid)
  	// 	fetch(''+SERVER_URL+'likePost', {
   //      method: 'post',
   //      headers: {'Content-Type': 'application/json',
   //        },
   //      body: JSON.stringify({
   //          userid,
   //          postid,
   //      })
   //    }).then(response => response.json())
   //      .then(result => {
   //      	console.log('Like Response is ',result)
   //  	})
   //      .catch(err => console.log('ERROR in SUBMITTING the like',err))

   //      this.retrieveLikes(); //to update likes once the user has liked the post
  	// }

  	submitLikeAS = async () => {
  		this.toggleIsLikedByCurrentUser();  //to change the color of the like thumb
  		const userid = this.props.currentUser.id;
  		const postid = this.props.post.postid;
  		const response = await fetch(''+SERVER_URL+'likePost', {
						        method: 'post',
						        headers: {'Content-Type': 'application/json',
						          },
						        body: JSON.stringify({
						            userid,
						            postid,
						        })
						      })
  		const result = await response.json();
  		console.log('submitLikeAS returned likeid', result);
        this.retrieveLikes(); //to update likes once the user has liked the post
  	}

 //  	retrieveMessagesS = async () =>{
	// 	const response = await fetch(''+SERVER_URL+'getAllMessages', {
	// 					    method: 'post',
	// 					    headers: {'Content-Type': 'application/json'},
	// 					    body: JSON.stringify({
	// 					    	fromUserId: this.props.currentUser.id,
	// 					    	toUserId: this.props.toUser.id,
	// 					    })
	// 				    })
	// 	const chat = await response.json();
	//     this.setState({chat:chat});
	// }



	render(){
		const post = this.props.post;
		const likes = this.state.likes;
		var displayLike = 'Like';
		if(this.state.isLikedByCurrentUser === false){
			displayLike = <div>
							<FontAwesomeIcon icon={faThumbsUp} size='2x'/>
							<b style={{verticalAlign:'super', marginLeft:'5px'}} >Like</b>
						 </div>
		}else{
			displayLike = <div>
				            <FontAwesomeIcon icon={faThumbsUp} size='2x' color="blue" />
						    <b style={{verticalAlign:'super', marginLeft:'5px'}} >Liked</b>
						  </div>
		}
		return(
			<div className='card postContainer'>
				<div style={{padding: '12px'}}>
					{/*Add the user profile image and name here*/}
				<img alt='user profile' style={{marginRight:'10px',width:'50px',verticalAlign:'top'}} src={'https://res.cloudinary.com/awesomedeveloper/image/upload/w_60,c_fill,ar_1:1,g_auto,r_max,bo_2px_solid_gray/' + post.profileimageid} />
					<div className='postUser'>
						<div style={{fontSize:'1.6rem',fontWeight:'900'}}>{post.firstname + " " + post.lastname}</div>
						<div style={{fontSize:'1.2rem'}}>{this.processDate(post.created)}</div>
					</div>	
				</div>
				<div style={{paddingLeft:'12px',paddingRight:'12px',paddingBottom:'6px',fontSize:'1.5rem',whiteSpace: 'pre-line'}}>
					{/*Add the post description here, whiteSpace:pre-line renders \n in description as new line*/}
						{post.description}
				</div>
				<div >
					{/*Add the post image here*/}
					<img alt='post' style={{width:'100%',height:'auto',borderTop:'1px solid gray', borderBottom:'1px solid gray'}} src={'https://res.cloudinary.com/awesomedeveloper/image/upload/c_scale,w_800/' + post.imagepath} />
				</div>
				<div style={{padding:'12px'}}>
					{/*Panel to like the post above*/}
					<div>
					{(likes === 0)?'':((likes ===1)? likes + " like" : likes + " likes")}
					</div>
					<hr className='hrline2'/>
					<div className='likeButtonContainer' onClick={this.submitLikeAS}>
						{displayLike}
					</div>
				</div> 
			</div>
		);
	}
}

export default Post;
