import React, { Component } from "react";
import { Grid, Row, Col,FormGroup,FormControl } from "react-bootstrap";
import { Redirect } from 'react-router-dom'
import Button from "components/CustomButton/CustomButton.jsx";
import Card from "components/Card/Card.jsx";
import Post from "components/Post/Post.jsx";
import {SERVER_URL} from "constants/nodeserver";
//import {NOTIFICATION_BODY_ARRAY, NOTIFICATION_SUBJECT_ARRAY} from 'constants/Notifications';
//import Sidebar from 'components/Sidebar/Sidebar'
import img1 from 'assets/img/sidebar-1.jpg';
class PostList extends Component {
  
  constructor(props){
    super(props);
    this.state = {
      postImagePublicId: '',
      posts:[],
    }
  }

//To redirect the user to login page if no valid user is signed in
  redirectToLogin = () => {
   // console.log('redirectToLogin called in PostList')
    const currentUser = this.props.currentUser;
    if(currentUser.id == null || currentUser.id.toString().length === 0){
      console.log('Redirecting to login page from postList')
      return <Redirect to='/login' />
    }else{
     // console.log('redirectToLogin to login did not redirect')
      return null;
    }
  }

  handleSubmit = (event) => {
      event.preventDefault();
      console.log('Submit Button Clicked', event.target.elements);
      let {description} = event.target.elements;
      const userid = this.props.currentUser.id;
      description = description.value;
      const postImagePublicId = this.state.postImagePublicId;

      if(!userid || userid.length=== 0 || !description){
        console.log('ERROR in addPost Form','All fields must be filled or invalid user');
        return
      }

      if(!postImagePublicId || postImagePublicId.length === 0){
        console.log('ERROR in addPost Form','No post Image selected');
        return
      }

      fetch(''+SERVER_URL+'addAPost', {
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            userid,
            description,
            imagepath: postImagePublicId,
        })
      }).then(response => response.json())
        .then(postid => console.log('PostId for the submitted post is ',postid))
        .catch(err => console.log('ERROR in SUBMITTING the addPost Form',err))

       document.getElementById('addPostForm').reset();
       this.setState({postImagePublicId:''}); //the image thumnail needs to be removed on submission
       this.sendNewPostNotification();
       this.retrievePosts();


    } //closing the handlesubmit

  uploadWidget = () => {
        window.cloudinary.openUploadWidget({ 
          cloud_name: 'awesomedeveloper',
        upload_preset: 'onkarpreset',
          cropping : 'server',
          croppingAspectRatio: 1,
          singleUploadAutoClose: false,
          thumbnails: false,
          multiple: false,
          resourceType: 'image',
          clientAllowedFormats:["png","gif", "jpeg"],
          maxFileSize: 5500000
        }, 
        (error, result) => { 
           if(error){
              console.log(error);
           }
           if (result && result.event === "success") {
              const path = result.info.path;
              console.log('path for the uploaded image is ', path);
              this.setState({postImagePublicId: path});
           }
        });
              
    }

    //To add the Cloudinary widget into the Signup Page
    componentDidMount(){
      this.retrievePosts();


      /* Widget Script Additon*/
          const script = document.createElement("script");
          script.src = "https://widget.cloudinary.com/v2.0/global/all.js";
          script.async = true;
          document.body.appendChild(script);
    }

    retrievePosts = () => {
      fetch(''+SERVER_URL+'getAllPostsV2', {
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            userid: this.props.currentUser.id
        })
      }).then(response => response.json())
        .then(posts => this.setState({posts:posts}) )
        .catch(err => console.log('ERROR in retriving posts',err))
    }

  sendNewPostNotification = () => {
      const currentUser = this.props.currentUser;
      fetch(''+SERVER_URL+'sendGlobalNotification', {
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            creator_id: currentUser.id,
            message_body: (currentUser.firstname +" "+ currentUser.lastname + ' added a new post'),
            subject:'New Post',
            recipient_type:'GLOBAL'
        })
      }).then(response => response.json())
        .then(result => console.log('The result for sendNewPostNotification is ', result))
        .catch(err => console.log('ERROR in retriving posts',err))
  }  
  

  render() {
    const currentUser = this.props.currentUser;
    const posts = this.state.posts;
     var uploadedImagePlaceholder;
    if(this.state.postImagePublicId.length != 0){
      uploadedImagePlaceholder = <Row><Col md={3}>
                                  <img alt='post' style={{width:'200px',height:'200px',borderTop:'1px solid gray', borderBottom:'1px solid gray'}} src={'https://res.cloudinary.com/awesomedeveloper/image/upload/c_scale,w_400/' + this.state.postImagePublicId} />
                                 </Col></Row>
    }else{
      uploadedImagePlaceholder = null;
    }
    return (

      <div className="content">
      {this.redirectToLogin()}
        <Grid fluid>
          <Row>
            <Col md={6}>
              <Card
                title="Add a post"
                category="Share something with the community"
                content={

                  <form onSubmit={this.handleSubmit} id='addPostForm'>
                    <Row>
                        <Col md={12}>
                          <FormGroup controlId="formControlsTextarea">
                            <FormControl
                              rows="3"
                              componentClass="textarea"
                              name="description"
                              bsClass="form-control"
                              placeholder={"What's on your mind, " + currentUser.firstname}
                              defaultValue=""
                            />
                          </FormGroup>
                        </Col>
                    </Row>
                    {uploadedImagePlaceholder}
                    <Row>
                        <Col md={3}>
                          <Button className='btn btn-info' onClick={this.uploadWidget} id="widget2">
                            Add an image
                          </Button>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={12} style={{textAlign:'Center'}}>
                          <Button bsStyle="info" style={{marginTop:'10px',width:'100%'}} simple fill type="submit">
                            Share with Community
                          </Button>
                        </Col>
                    </Row>
                  </form>

                }
              />
            </Col>
          </Row>
          { posts.map((post,key) => {
            return(
            <Row key={key}>
              <Col md={6}>
              <Post post={post} currentUser={this.props.currentUser} />
              </Col>
            </Row>
          );
          })
        }
        </Grid>
      </div>
    );
  }
}

export default PostList;
