import React, {Component} from 'react';
import {SERVER_URL} from 'constants/nodeserver';

class Verification extends Component{
	constructor(props){
		super(props);
		this.state = {
			vStatus: 10 //vStatus 10 means loading
		}
	}

	componentDidMount(){
		let search = window.location.search;
		let params = new URLSearchParams(search);
		let id = params.get('id');
		let email = params.get('email');
		//console.log('id', id, 'email', email);

		//The verification call
	    fetch(SERVER_URL+'verify', {
	      method: 'post',
	      headers: {'Content-Type': 'application/json'},
	      body: JSON.stringify({
	        email: email,
	        id: id
	      })
	    }).then(response => response.json())
	    .then(result => {
	    	this.setState({vStatus: result.toString()});
	    	console.log('result of verify from backend is', result)
	    })
	}

	render(){
		const currHost = window.location.origin; //returns the protocol + hostname of the react frontend
		var displayContent = null;
		console.log("vStatus in state is", this.state.vStatus);
		if(this.state.vStatus === '1'){
			displayContent = <div>
								<h3>Verfication Status: Verified</h3>
								<a href = {currHost + "/login"}> Login Here To Continue</a>
							</div>
		}else if(this.state.vStatus === '-3'){
			displayContent = <div>
								<h3>Your account is already verified</h3>
								<a href = {currHost + "/login"}> Login Here To Continue</a>
							</div>
		}else{
			displayContent = <div>
							<h3>Verification Failed!</h3>
							<h4>Please contact your system administrator for more information</h4>
							</div>
		}

	
		return(
			<div className="content">
				{displayContent}
			</div>
		);
	}
}

export default Verification;