import React, { Component } from "react";
import { Grid, Row, Col } from "react-bootstrap";
import { Redirect } from 'react-router-dom'
import Card from "components/Card/Card";
import UserTab from 'components/UserTab/UserTab.jsx';
import {SERVER_URL} from "constants/nodeserver";

class Community extends Component {
  constructor(props){
    super(props);
    this.state = {
      usersArray : [],
    }
  }

  //To redirect the user to login page if no valid user is signed in
  redirectToLogin = () => {
    //console.log('redirectToLogin called in TableList')
    const currentUser = this.props.currentUser;
    if(currentUser.id == null || currentUser.id.toString().length === 0){
      console.log('Redirecting to login page from Community')
      return <Redirect to='/login' />
    }else{
      //console.log('redirectToLogin to login did not redirect')
      return null;
    }
  }

  componentDidMount(){
    const {currentUser} = this.props;
    const searchUserKeyword = this.props.searchUserKeyword;

    if(!currentUser.id || currentUser.id.length === 0){
        console.log('Warning currentUser is null or empty in Community.jsx in componentDidMount');
        return;
     } 

    // if(searchUserKeyword !== null){
    //   this.fetchSearchUsers(searchUserKeyword);
    //   return;
    // }
    fetch(''+SERVER_URL+'getAllUsersCommunity',{
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            userid: currentUser.id 
        })
      })
      .then(response => response.json())
      .then(users => {  
        const mUsers = this.makeUserIdString(users)
        this.setState({usersArray: mUsers})
              console.log('all usersArray now is: ',mUsers)})
      .catch(err => console.log(err))
  }

  componentDidUpdate(prevProps) {
  // Typical usage (don't forget to compare props):
  if (this.props.searchUserKeyword !== prevProps.searchUserKeyword) {
    this.fetchSearchUsers();
  }
}

  fetchSearchUsers = () => {
    const searchKeyword = this.props.searchUserKeyword;
    console.log('running fetchSearchUsers with keyword ', searchKeyword)
      fetch(''+SERVER_URL+'searchUsersv2',{
        method: 'post',
        headers: {'Content-Type': 'application/json',
          },
        body: JSON.stringify({
            searchKeyword,
            userid: this.props.currentUser.id,
        })
      })
      .then(response => response.json())
      .then(users => {  
        const mUsers = this.makeUserIdString(users)
        this.setState({usersArray: mUsers})
              console.log('searched usersArray is: ',mUsers)})
      .catch(err => console.log(err))
  }

  makeUserIdString = (users) => {
    const mArray = users.map(user => {
      user.id = user.id.toString();
      return user;
    })
    return mArray;
  }

  render() {
    var displayContent;
    if(this.state.usersArray.length > 0){
      displayContent = <Row>
                    {
                      this.state.usersArray.map((prop) => {
                      return (
                        <Col
                          lg={4}
                          md={4}
                          sm={6}
                          xs={12}
                          key={prop.id}
                        >
                          <UserTab cardType='community' currentUser={this.props.currentUser} user={prop} key={prop.id} changeToUser={this.props.changeToUser} />
                        </Col>
                      );
                    })
                    
                    }
                  </Row>
                  
    }if(this.state.usersArray.length == 0){
      displayContent = <div>
                          <span>Loading...</span>
                       </div>
    } 
    return (
      <div className="content">
      {this.redirectToLogin()}
        <Grid fluid>
          <Row>
            <Col md={12}>
              <Card
                title="Our Community"
                ctAllIcons
                category={
                  <span>
                    Handcrafted by our creator <b>Onkar Singh</b>
                  </span>
                }
                content={displayContent}
              />
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}

export default Community;
