import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import HeaderLinks from "../Header/HeaderLinks.jsx";
import imagine from "assets/img/sidebar-3.jpg";
import logo from "assets/img/reactlogo.png";
import dashboardRoutes from "routes/dashboard.jsx";

class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth
    };
  }
  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? "active" : "";
  }
  updateDimensions() {
    this.setState({ width: window.innerWidth });
  }
  componentDidMount() {
    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));
  }
  render() {
    const sidebarBackground = {
      backgroundImage: "url(" + imagine + ")"
    };
    var sidebarHeaderLink = null; 
    if(this.props.currentUser.id.toString().length !== 0){
      sidebarHeaderLink = <HeaderLinks 
                      currentUser={this.props.currentUser} 
                      changeSearchUserKeyword={this.props.changeSearchUserKeyword}
                      logoutUser = {this.props.logoutUser}
                    />
    }        
    return (
      <div
        id="sidebar"
        className="sidebar"
        data-color="black"
        data-image={imagine}
      >
        <div className="sidebar-background" style={sidebarBackground} />
        <div className="logo">
          <a
            href="#"
            className="simple-text logo-mini"
          >
            <div className="logo-img">
              <img src={logo} alt="logo_image" />
            </div>
          </a>
          <a
            href="#"
            className="simple-text logo-normal"
          >
            Sheridan Unite
          </a>
        </div>
        <div className="sidebar-wrapper">
          <ul className="nav">
            {
            this.state.width <= 991 ? sidebarHeaderLink : null
            }
            {dashboardRoutes.map((prop, key) => {
              //To display only signin and signup when user isn't signed in
              if(this.props.currentUser.id === ''){
                if(prop.name !== 'Login' && prop.name !== 'User Signup')
                  return null
              }
              if(this.props.currentUser.id !== ''){
                if(prop.name == 'User Signup')
                  return null
              }
              //if user is logged in and the prop is login then don't show it on the sidebar
              if(prop.name === 'Login' && this.props.currentUser.id !== ''){ //If the user is logged in and the componenet is login then don't return null
                  return null;
              }
              if(prop.invisible){
               return null;   
              }
              if (!prop.redirect)
                return (
                  <li
                    className={
                      prop.upgrade
                        ? "active active-pro"
                        : this.activeRoute(prop.path)
                    }
                    key={key}
                  >
                    <NavLink
                      to={prop.path}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <i className={prop.icon} />
                      <p>{prop.name}</p>
                    </NavLink>
                  </li>
                );
              return null;
            })}
          </ul>
        </div>
      </div>
    );
  }
}

export default Sidebar;
