import React, { Component } from "react";
import { Navbar } from "react-bootstrap";
import { Redirect } from 'react-router-dom'

import HeaderLinks from "./HeaderLinks.jsx";

import dashboardRoutes from "routes/dashboard.jsx";

class Header extends Component {
  constructor(props) {
    super(props);
    this.mobileSidebarToggle = this.mobileSidebarToggle.bind(this);
    this.state = {
      sidebarExists: false
    };
  }

  redirectToLogin = () => {
    const currentUser = this.props.currentUser;
    if(currentUser.id == null || currentUser.id.toString().length === 0){
      console.log('Redirecting to login page')
      return <Redirect to='/login' />
    }else{
      console.log('user exists')
    }
  }

  // mobileSidebarToggle(e) {
  //   if (this.state.sidebarExists === false) {
  //     this.setState({
  //       sidebarExists: true
  //     });
  //   }
  //   e.preventDefault();
  //   document.documentElement.classList.toggle("nav-open");
  //   var node = document.createElement("div");
  //   node.id = "bodyClick";
  //   node.onclick = function() {
  //     this.parentElement.removeChild(this);
  //     document.documentElement.classList.toggle("nav-open");
  //   };
  //   document.body.appendChild(node);
  // }


   mobileSidebarToggle(e) {
    if (this.state.sidebarExists === false) {
      this.setState({
        sidebarExists: true
      });
    }
    e.preventDefault();
    document.documentElement.classList.toggle("nav-open");
    var node = document.createElement("div");
    node.id = "bodyClick";
    node.onclick = function() {
      this.parentElement.removeChild(this);
      document.documentElement.classList.toggle("nav-open");
    };
    document.body.appendChild(node);
   }
  
  getBrand() {
    var name;
    dashboardRoutes.map((prop, key) => {
      if (prop.collapse) {
        prop.views.map((prop, key) => {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
          return null;
        });
      } else {
        if (prop.redirect) {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
        } else {
          if (prop.path === this.props.location.pathname) {
            name = prop.name;
          }
        }
      }
      return null;
    });
    return name;
  }
  render() {
    // var rightNav = <div>
    //                 <HeaderLinks 
    //                 currentUser={this.props.currentUser} 
    //                 changeSearchUserKeyword={this.props.changeSearchUserKeyword}
    //                 logoutUser = {this.props.logoutUser}
    //                  />
    //               </div>;
      var rightNav = <Navbar.Collapse>
                    <HeaderLinks 
                    currentUser={this.props.currentUser} 
                    changeSearchUserKeyword={this.props.changeSearchUserKeyword}
                    logoutUser = {this.props.logoutUser}
                     />
                  </Navbar.Collapse>;
    if(this.props.currentUser.id.toString().length === 0){
      rightNav = null;
    }
    return ( 
      <Navbar fluid>
        <Navbar.Header>
          <Navbar.Brand>
             <a href="#pablo">{this.getBrand()}</a>
          </Navbar.Brand>
          <Navbar.Toggle onClick={this.mobileSidebarToggle} />
        </Navbar.Header>
        {rightNav}
      </Navbar>
    );
  }
}

export default Header;
