import React, { Component } from "react";
import { Redirect } from 'react-router-dom'
import { Grid, Row, Col} from "react-bootstrap";
import { FormInputs } from "components/FormInputs/FormInputs.jsx";
import Card from "components/Card/Card.jsx";
import GroupImage from 'assets/img/group.jpg';
import Button from "components/CustomButton/CustomButton.jsx";
import {SERVER_URL} from "constants/nodeserver";

class Login extends Component {

  constructor(props){
    super(props);
    this.state = {
      invalidSiginError:'',
      redirect:false,
    }
  }

  componentDidMount(){
    //var user = ls.get('currentUser') || this.props.emptyUserTemplate;
    if(this.props.currentUser.id){
      this.setRedirect();
    }
    console.log('currentUser',this.props.currentUser);
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }

  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to='/posts' />
    }
  }

  submitSignInForm = (event) => {
      event.preventDefault();
    console.log("SignIn Form is submitted!!");

    const inputEmail = event.target.elements.email.value;
    const inputPassword = event.target.elements.password.value;
    //let userMatched = false;

    if(inputEmail === '' || inputPassword === ''){
      console.log('Input email or password is empty');
      return;
    }

    //The signin fetch call
    fetch(''+SERVER_URL+'login', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        email: inputEmail,
        password: inputPassword
      })
    }).then(response => response.json())
    .then(user => {
      console.log('User Login Result:' + user);
      if(user.email){
        //changing the currentUser to the user who just signed in
        this.props.changeCurrentUser(user);
        console.log(user.email);
        this.setState({invalidSiginError:''});
        this.setRedirect();
      }else if(user === '-4'){
        this.props.handleClick('User account not verfied yet. Please check your email for verification link', 3);
        console.log("User account not verified yet");
      }else{
        console.log('User Not Found');
        this.props.handleClick('Sorry! Username or Password is incorrect', 3);
        this.setState({invalidSiginError:'Invalid Username or Password'});
      }
    }) 
  }

  render() {
    return (
      <div className="content">
    {/*To check if the user logged in successfully then show the dashboard*/}
      {this.renderRedirect()}
        <Grid fluid>
          <Row>

            <Col md={6}>
              <Card
                title="SignIn"
                category="Please fill in your email and password to signin"
                content={
                  <div>
                    <form onSubmit={this.submitSignInForm} id='signinform'>
                    <FormInputs
                      ncols={["col-md-12"]}
                      proprieties={[
                        {
                          label: "Email",
                          type: "email",
                          bsClass: "form-control",
                          placeholder: "Email",
                          defaultValue: "",
                          name:'email'
                        }
                      ]}
                    />
                    <FormInputs
                      ncols={["col-md-12"]}
                      proprieties={[
                        {
                          label: "Password",
                          type: "password",
                          bsClass: "form-control",
                          placeholder: "Password",
                          defaultValue: "",
                          name:'password'
                        }
                      ]}
                    />
                    <Row>
                      <Col md={12} className='text-center font-weight-bold'>
                      {this.state.invalidSiginError}
                      </Col>
                    </Row>
                     <Row>
                      <Col md={12} className="text-center">
                        <Button bsStyle="primary" simple fill type="submit">
                          SignIn
                        </Button>
                      </Col>
                    </Row>
                   <div className="clearfix" />
                    </form>
                  </div>
                }/>
            </Col>

            <Col md={6} className='border border-primary bg-dark'>
            <div className='card' >
              <div className='image' style={{height:'333px'}}>
              <img
                className="img-fluid"
                src={GroupImage}
                alt="..."
              />
              </div>
            </div>
          
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}

export default Login;
