import React, {Component} from 'react';
import { Redirect } from 'react-router-dom'
import {SERVER_URL} from "constants/nodeserver";


class UserTab extends Component{
  constructor(props){
    super(props);
    this.state = {
      redirect:false,
      isRequestSent: ((this.props.user.frid === null)?false:true),
    }
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }

  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to='/chatBox' />
    }
  }

  //user is the user whose profile is being shown in the usertab
  //changeToUser is the method to change the toUser in the App.js; elevated state
  //currentUser is the user who is currently logged in
  handleChatClick = () => {
      const {user, changeToUser, currentUser} = this.props;
      if(!user.id || !currentUser.id || user.id.length === 0 || currentUser.id.length === 0){
        console.log('user and/or currentUser are null or empty');
        return;
      } 
      changeToUser(user);
      this.setRedirect();
  }

  sendFriendRequest = () => {
     const {user, currentUser} = this.props;
     console.log('CurrentUser firstname is ', currentUser.firstname, ' and the user firstname is ', user.firstname);
     if(!user.id || !currentUser.id || user.id.length === 0 || currentUser.id.length === 0){
        console.log('user and/or currentUser are null or empty');
        return;
     } 
     fetch(''+SERVER_URL+'sendFriendRequest', {
          method: 'post',
          headers: {'Content-Type': 'application/json'},
          body: JSON.stringify({
            senderid: currentUser.id,
            receiverid: user.id,
          })
        }).then(res => res.json())
          .then(result => {console.log('Response for sending Friend request is ', result)
                           if(!isNaN(result)){
                              //to send the friend request to the receiver of the friend request      
                              this.sendFriendRequestNotification();
                              this.setState({isRequestSent:true})
                           }
                          })
          .catch(err => console.log(err))
  }

   //using the synchronos version of the acceptFriendRequest calls the refetchFriendRequests
   //method only after the database has been updated with the accepted friend request.
   //In other words removes the usertab from the view once the user presses the accept button
   acceptFriendRequestAS = async () => {
    const {user, currentUser} = this.props;
    if(!user.id || !currentUser.id || user.id.length === 0 || currentUser.id.length === 0){
        console.log('user and/or currentUser are null or empty');
        return;
     } 
     try{
       const response = await fetch(''+SERVER_URL+'acceptFriendRequest', {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
              senderid: user.id,
              receiverid: currentUser.id,
            })
          })
       const result = await response.json();
       console.log('Response for accepting Friend request is ', result);
     }catch(error){
       console.log(error);
     }
     this.acceptFriendRequestNotification();
     this.props.refetchFriendRequests();    
  }

  deleteFriendRequest = () => {
    const {user, currentUser} = this.props;
    if(!user.id || !currentUser.id || user.id.length === 0 || currentUser.id.length === 0){
        console.log('user and/or currentUser are null or empty');
        return;
     } 
    console.log('deleteFriendRequest called in UserTab for senderid and recieverid ', currentUser.id, user.id);
    fetch(''+SERVER_URL+'deleteFriendRequest', {
      method: 'post',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({
        receiverid: currentUser.id,
        senderid: user.id,
      })
    }).then(res => res.json())
    .then(result => {
                      console.log('The result of deleteFriendRequest is ', result);
                      if(!isNaN(result)){
                        this.setState({isRequestSent:false})
                      }
                     })
    .catch(err => console.log('Error occured while deleting friend request', err))
  }




    deleteFriendRequestAS = async () => {
    const {user, currentUser} = this.props;
    if(!user.id || !currentUser.id || user.id.length === 0 || currentUser.id.length === 0){
        console.log('user and/or currentUser are null or empty');
        return;
     } 
    console.log('deleteFriendRequest called in UserTab for senderid and recieverid ', currentUser.id, user.id);
    try{
      const response = await fetch(''+SERVER_URL+'deleteFriendRequest', {
        method: 'post',
        headers: {'Content-Type':'application/json'},
        body: JSON.stringify({
          receiverid: currentUser.id,
          senderid: user.id,
        })
      })
      const result = await response.json();
      console.log('The result of deleteFriendRequest is ', result);
                      // if(!isNaN(result)){
                      //   this.setState({isRequestSent:false})
                      // }
     }catch(err){
      console.log('Error occured while deleting friend request', err)
    }
    this.props.refetchFriendRequests();    
  }

  sendFriendRequestNotification = () => {
    const {user, currentUser} = this.props;
    fetch(''+SERVER_URL+'sendNotificationSingle', {
      method: 'post',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({
        creator_id : currentUser.id,
        message_body: (currentUser.firstname + ' ' + currentUser.lastname + ' wants to be your friend'),
        subject:'Friend Request',
        recipient_type: 'SINGLE',
        recipient_id: user.id
      })
    }).then(res => res.json())
    .then(result => console.log('The result of sendFriendRequestNotification is ', result))
    .catch(err => console.log('Error occured while sending friend request notificaiton in UserTab'))
  }


  acceptFriendRequestNotification = () => {
    const {user, currentUser} = this.props;
    fetch(''+SERVER_URL+'sendNotificationSingle', {
      method: 'post',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({
        creator_id : currentUser.id,
        message_body: (currentUser.firstname + ' ' + currentUser.lastname + ' accepted your friend request'),
        subject:'Accepted Friend Request',
        recipient_type: 'SINGLE',
        recipient_id: user.id
      })
    }).then(res => res.json())
    .then(result => console.log('The result of acceptFriendRequestNotification is ', result))
    .catch(err => console.log('Error occured while accepting friend request notificaiton in UserTab'))
  }

  render(){
    const {user, cardType} = this.props;
    //const isRequestSent = (user.frid === null)?false:true;
    //this.setState({isRequestSent})
    const imgSrc = 'https://res.cloudinary.com/awesomedeveloper/image/upload/c_scale,h_300,w_300/' + user.profileimageid;
    // if(user.id == currentUser.id){
    //   return(
    //     <div className="card" style={{padding:'10px',width: '220px', border:'3px solid gray', fontFamily:'inherit', marginLeft:'auto',marginRight:'auto'}}>
    //     {this.renderRedirect()}
    //   <div className="card-img-top text-center" style={{borderRadius:'3px'}}>
    //     <img style={{width:'192px', border:'1px solid black'}} src={imgSrc} alt="Profile"/>
    //   </div>
    //     <div className="card-body" style={{marginTop:'5px'}}>
    //       <b className="card-title" style={{fontSize:'1.8rem'}}>{user.firstname + " " + user.lastname}</b>
    //       <p style={{fontSize:'1.4rem'}}>{user.tagline}</p>
    //       <button  className="btn btn-primary">Modify Your Account</button>
    //     </div>
    //   </div>
    //   );
    //}
    
    if(user.friendsid !== null && user.friendsid !== undefined){
       return(
        <div className="card" style={{padding:'10px',width: '220px', border:'3px solid gray', fontFamily:'inherit', marginLeft:'auto',marginRight:'auto'}}>
        {this.renderRedirect()}
      <div className="card-img-top text-center" style={{borderRadius:'3px'}}>
        <img style={{width:'192px', border:'1px solid black'}} src={imgSrc} alt="Profile"/>
      </div>
        <div className="card-body" style={{marginTop:'5px'}}>
          <b className="card-title" style={{fontSize:'1.8rem'}}>{user.firstname + " " + user.lastname}</b>
          <p style={{fontSize:'1.4rem'}}>{user.tagline}</p>
          <button onClick={this.handleChatClick} className="btn btn-primary">Chat</button>
          <button className="btn btn-primary" style={{marginLeft:'10px'}}>
           Friends
           <i className="fa fa-check" />
          </button>
        </div>
      </div>
      );
    }
    if(cardType === 'community'){
      return(
        <div className="card" style={{padding:'10px',width: '220px', border:'3px solid gray', fontFamily:'inherit', marginLeft:'auto',marginRight:'auto'}}>
        {this.renderRedirect()}
      <div className="card-img-top text-center" style={{borderRadius:'3px'}}>
        <img style={{width:'192px', border:'1px solid black'}} src={imgSrc} alt="Profile"/>
      </div>
        <div className="card-body" style={{marginTop:'5px'}}>
          <b className="card-title" style={{fontSize:'1.8rem'}}>{user.firstname + " " + user.lastname}</b>
          <p style={{fontSize:'1.4rem'}}>{user.tagline}</p>
          <button onClick={this.handleChatClick} className="btn btn-primary">Chat</button>
          {
            (this.state.isRequestSent)
             // ? (<button onClick={() => {this.deleteFriendRequest(user.frid)}} className="btn btn-danger" style={{marginLeft:'10px'}}>Cancel</button>)
              ? (<button className="btn btn-success disabled" style={{marginLeft:'10px'}}>Request Sent</button>)
              : (<button onClick={this.sendFriendRequest} className="btn btn-primary" style={{marginLeft:'10px'}}>Add Friend</button>)
          }
        </div>
      </div>
      );
    }
    if(cardType === 'friendRequest'){
        return(
        <div className="card" style={{padding:'10px',width: '220px', border:'3px solid gray', fontFamily:'inherit', marginLeft:'auto',marginRight:'auto'}}>
        {this.renderRedirect()}
      <div className="card-img-top text-center" style={{borderRadius:'3px'}}>
        <img style={{width:'192px', border:'1px solid black'}} src={imgSrc} alt="Profile"/>
      </div>
        <div className="card-body" style={{marginTop:'5px'}}>
          <b className="card-title" style={{fontSize:'1.8rem'}}>{user.firstname + " " + user.lastname}</b>
          <p style={{fontSize:'1.4rem'}}>{user.tagline}</p>
          <button onClick={this.acceptFriendRequestAS} className="btn btn-primary">Accept</button>
          <button onClick={this.deleteFriendRequestAS} className="btn btn-primary" style={{marginLeft:'10px'}}>Delete</button>
        </div>
      </div>
      );
    }else{
      return <h5> No CardType specified </h5>
    }
  }
}

export default UserTab;
