import React, {Component} from 'react';
import {
  Grid,
  Row,
  Col
} from "react-bootstrap";
import io from "socket.io-client";
import { Card } from "components/Card/Card.jsx";
import { Redirect } from 'react-router-dom'
import {SERVER_URL} from "constants/nodeserver";
 
class ChatBox extends Component{
	constructor(props){
			super(props);
			this.state={
				chat:[],
				message:'',
				isOtherUserOnline: false,
				currentSocket:{},
			}

	    this.socket = io(SERVER_URL);
	    console.log('this.socket is ', this.socket);
	    //this.setState({currentSocket:this.socket});
        const chatRoomId = this.createChatRoomId();
        this.socket.emit('enrollChatRoom', {chatRoomId: chatRoomId})
        this.sendMessage = ev => {
            ev.preventDefault();
        	const message = ev.target.elements.message.value;
        	if(message === null || message.length === 0){
        		console.log('Chat Msg length zero')
        		return;
        	}
		    this.socket.emit('SEND_MESSAGE', {
		        fromuserid: this.props.currentUser.id,
			    touserid: this.props.toUser.id,
			    message:ev.target.elements.message.value
		    });
		    this.setState({message: ''});
		    ev.target.elements.message.value = ''; //to clear the msg input box
		}
		this.socket.on('RECEIVE_MESSAGE', function(data){
    		addMessage(data);
		});
		const addMessage = data => {
		    console.log(data);
		    this.setState({chat: [...this.state.chat, data]});
		    console.log('Socket chat is ',this.state.chat);
		    this.scrollToBottomS();
		};
		this.socket.on('DISCONNECTED', function(){
    		this.setState({isOtherUserOnline: false});
		});
		this.socket.on('CONNECTED', function (){
    		this.setState({isOtherUserOnline: true});
		});
		this.scrollToBottomS = () => {
		const msgDiv = document.getElementById("msgDiv");
		console.log('msgDiv',msgDiv)
     	msgDiv.scrollTop = msgDiv.scrollHeight - msgDiv.clientHeight;
		}		

	}  //end of construtor

	//To redirect the user to login page if no valid user is signed in
	redirectToLogin = () => {
	    console.log('redirectToLogin called in TableList')
	    const currentUser = this.props.currentUser;
	    if(currentUser.id == null || currentUser.id.toString().length === 0){
	      console.log('Redirecting to login page')
	      return <Redirect to='/login' />
	    }else{
	      console.log('redirectToLogin to login did not redirect')
	      return null;
	    }
	}

	createChatRoomId = () => {
			const cu = this.props.currentUser.id;
			const tu = this.props.toUser.id;
			if(cu > tu){
				return ''+tu + "#" + cu;
			}else{
				return ''+cu + "#" + tu;
			}
		}

	componentDidMount(){
		this.retrieveMessages();
	}

	componentWillUnmount(){
		//disconnecting the chatsocket
		this.socket.disconnect();
	}

	retrieveMessages = () =>{
		console.log("retrieveMessages ran now")
		fetch(''+SERVER_URL+'getLimitedMessages', {
			    method: 'post',
			    headers: {'Content-Type': 'application/json'},
			    body: JSON.stringify({
			    	fromUserId: this.props.currentUser.id,
			    	toUserId: this.props.toUser.id,
			    })
		    }).then(res => res.json())
			.then(chat => {
				chat.reverse(); //to reverse the chats so that they are displayed accordingly
				//As the server is returning a limited chat ordered by desc date
				console.log('retrieveMessages retrived chat', chat);
				this.setState({chat:chat})
				})
			.catch(err => console.log('Error in retrieveMessages in Chatbox',err))
	}

	render(){
		const {currentUser, toUser} = this.props;
		const chat = this.state.chat;
		currentUser.id = currentUser.id + "";
//ba bg-dark-red white pa1 ph3 br4
		const msgDialogs = chat.map((msg,i) => {
			console.log('id comparison',i,msg.fromuserid, currentUser.id, msg.fromuserid.toString() === currentUser.id.toString())
			if(msg.fromuserid.toString() === currentUser.id.toString()){
				return 	<div style={{padding:'5px', textAlign:'right',marginBottom:'5px'}} key={i}><span className='msgSpanCurrentUser' >{msg.message}</span></div>
			}else {
				return 	<div style={{padding:'5px', textAlign:'left',marginBottom:'5px'}} key={i}><span className='msgSpanToUser'>{msg.message}</span></div>
			}
		})
		return(
		 <div className="content" >
		 {this.redirectToLogin()}
	         <Grid fluid>
	          <Row>
	            <Col lg={6}
	            	 md={7}
	            	 sm={8}>
	              <Card
	                title="ChatBox"
	                content={
	                	<div className='card' style={{padding:'10px'}}>
	                		<div>
	                			<h4 style={{marginTop:'0px'}}>{toUser.firstname + " " + toUser.lastname}</h4>
	                			<h5>{this.state.isOtherUserOnline ? 'Online' : 'Offline'}</h5>
	                		</div>
	                		<hr className='hrline'/>
	                		<div style={{height:'350px', overflowY: 'auto'}} id='msgDiv'>
	                			{/* Insert the chat here */
	                				msgDialogs
	                			}
	                		</div>
	                		<hr className='hrline'/>
	                		<div>
	                			<form method='post' onSubmit={this.sendMessage}>
	                				<input style={{width:'75%', display:'inline-block', border: '2px solid gray'}} className='form-control' type='text' name='message' id='inputMessage' placeholder='Type your messgage to send'/>
	                		        <button style={{width:'15%',minWidth:'60px', display:'inline-block', marginLeft:'10px',textAlign:'center'}} className='btn btn-primary'>Send</button>	    
	                			</form>
	                		</div>
	                	</div>
	                }
	              />
	             </Col>
	           </Row>
	          </Grid>
         </div>   
		);
	}
}

export default ChatBox;