import React from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import indexRoutes from "./routes/index.jsx";
import ls from 'local-storage';
import SimpleCrypto from 'simple-crypto-js';

class App extends React.Component{
	simpleCrypto = new SimpleCrypto('kgf');

	constructor(){
		super();
		this.emptyUserTemplate =  {
			firstname:'',
			lastname:'',
			id:'',
			joined:'',
			tagline:''
		 }
		this.state={
		  currentUser: this.decryptUser() || this.emptyUserTemplate,
		  toUser:this.emptyUserTemplate
		}
	}

	decryptUser = () => {
		var user;
		var encryptedUser = ls.get('currentUser') || undefined;
		//console.log('decryptUser Method: encryptedUser is - ', encryptedUser);
		if(encryptedUser === undefined){
			user = null;
		}
		else{
		  var userStr = this.simpleCrypto.decrypt(encryptedUser);
		  var userObj = JSON.parse(userStr);
		  user = userObj;
		}
		//console.log('User returning from decryptUser method is', user);
		return user;
	}

	updateEncryptUser = (user) => {
		var userStr = JSON.stringify(user);
		var encryptedUser = this.simpleCrypto.encrypt(userStr);
		ls.set('currentUser', encryptedUser);
	}

	componentDidMount(){
		//console.log('store',this.props.store.getState());
	}


	changeCurrentUser = (user) => {
		if(user.id){
			this.setState({currentUser:user});
			this.updateEncryptUser(user);
		}
		console.log('changeCurrentUser in App.js called with user ', user);
	}

	logoutUser = () => {
		console.log('Logout user called in App.js');
		ls.clear();
		this.setState({currentUser: this.emptyUserTemplate });
	}

	changeToUser = (user) => {
		if(user.id){
			this.setState({toUser:user});
		}
		console.log('changeToUser in App.js called with user ', user);
	}


	/*
<Route
        path="/about"
        render={props => <About {...props} extra={someVariable} />}
      />
	
	 {indexRoutes.map((prop, key) => {
		        return <Route to={prop.path} component={prop.component} key={key} />;
		      })}
		      */

	
	render(){
		return(
		<BrowserRouter>
		      <Switch>
            {indexRoutes.map((prop, key) => {
            	
		        return <Route to={prop.path} render={props => <prop.component {...props} 
		        									currentUser={this.state.currentUser}
		        									emptyUserTemplate = {this.emptyUserTemplate} 
		        									changeCurrentUser={this.changeCurrentUser}
		        									logoutUser = {this.logoutUser}
		        									toUser={this.state.toUser}
		        									changeToUser={this.changeToUser}/>} key={key} />;
		      })}
        </Switch>
		</BrowserRouter>
		);
	}
}

export default App;